import moment from "moment";

const timeSlots = ({ startTime }) => {
  const start = moment(new Date(startTime));

  const startDate = start.format("YYYY-MM-DD");
  const todayDate = moment().format("YYYY-MM-DD");

  if (startDate < todayDate) {
    return [];
  } else {
    return [
      {
        name: "02:45 PM",
        value: 1568583900
      },
      {
        name: "03:00 PM",
        value: 1568584800
      },
      {
        name: "03:15 PM",
        value: 1568585700
      },
      {
        name: "03:30 PM",
        value: 1568586600
      },
      {
        name: "03:45 PM",
        value: 1568587500
      },
      {
        name: "04:00 PM",
        value: 1568588400
      },
      {
        name: "04:15 PM",
        value: 1568589300
      },
      {
        name: "04:30 PM",
        value: 1568590200
      },
      {
        name: "04:45 PM",
        value: 1568591100
      }
    ];
  }
};

export default timeSlots;



